import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { NgxSpinnerService } from 'ngx-spinner';
import { SuperAdminService } from 'src/app/Services/super-admin.service';

@Component({
  selector: 'app-store-review-details',
  templateUrl: './store-review-details.component.html',
  styleUrls: ['./store-review-details.component.scss']
})
export class StoreReviewDetailsComponent implements OnInit {

  openslider: boolean = false;
  storeId: any;
  element: any = [];
  storeReviewById: any = [];
  review: any = [];
  p: number = 1;
  constructor(private activatedRoute: ActivatedRoute, private superAdminService: SuperAdminService, private SpinnerService: NgxSpinnerService
  ) { }


  ngOnInit(): void {
    this.SpinnerService.show();
    this.getStoreReview();
  }
  getFeedbackReview(ele: any) {
    this.review = [];
    this.review = ele;

    console.log(this.review);
    this.SpinnerService.show();
    this.superAdminService.getFeedbackForReview(ele._id).subscribe((res: any) => {
      console.log(res);
      this.storeReviewById = [];
      this.storeReviewById = res.data;
      this.SpinnerService.hide();
    }, (error) => {
      console.log(error);
      this.SpinnerService.hide();
    })
  }
  getStoreReview() {
    this.storeId = this.activatedRoute.snapshot.paramMap.get('id');
    if (this.storeId) {
      this.superAdminService.getStoreReviewById(this.storeId).subscribe((res: any) => {
        console.log(res);
        this.element = res.data;
        console.log(this.element);

      })
    }
    this.SpinnerService.hide();
  }
  deleteStoreReviewById(ele: any) {
    console.log(ele);
    this.SpinnerService.show();
    this.superAdminService.deleteReviewById(ele).subscribe((res: any) => {
      console.log(res);
      this.element = [];
      this.getStoreReview();

    })

  }

  updateStoreReviewById(data: any) {
    this.SpinnerService.show();
    const status = data.isApproved = !data.isApproved;
    console.log(status);
    const body = {
      isApproved: status
    }
    this.superAdminService.updateReviewStatus(data._id, body).subscribe((res: any) => {
      console.log(res);
      for (var i = 0; i < this.element.length; i++) {
        this.element.forEach((ele: any) => {
          if (data._id === ele._id) {
            ele.isApproved = status
          }
        });
      }
      console.log(this.element);
      this.SpinnerService.hide();
    }, (error) => {
      this.SpinnerService.hide();
    })
  }
  // open mobile slider
  clickEvent(event: any) {

    this.openslider = !this.openslider;
  }
}
