import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { NgxSpinnerService } from 'ngx-spinner';
import { SuperAdminService } from 'src/app/Services/super-admin.service';

@Component({
  selector: 'app-food-review-details',
  templateUrl: './food-review-details.component.html',
  styleUrls: ['./food-review-details.component.scss']
})
export class FoodReviewDetailsComponent implements OnInit {
  openslider: boolean = false;
  storeId: any;
  element: any = [];
  foodReviewFeedback: any = [];
  review: any = [];
  p: number = 1;
  constructor(private activatedRoute: ActivatedRoute, private SpinnerService: NgxSpinnerService, private superAdminService: SuperAdminService) { }


  ngOnInit(): void {
    this.SpinnerService.show();
    this.getStoreReview();
  }
  getFeedbackReview(id: any, item: any) {
    this.review = [];
    this.review = item;
    console.log(this.review);

    console.log(id);
    this.SpinnerService.show();
    this.superAdminService.getFeedbackForFoodReview(id).subscribe((res: any) => {
      this.foodReviewFeedback = res.data;
      console.log(res);
      this.SpinnerService.hide();

    }, (eroor) => {
      this.SpinnerService.hide();
    })
  }
  getStoreReview() {
    this.storeId = this.activatedRoute.snapshot.paramMap.get('id');
    if (this.storeId) {
      this.superAdminService.getFoodReviewById(this.storeId).subscribe((res: any) => {
        console.log(res);
        this.element = res.data;
        this.SpinnerService.hide();
        console.log(this.element);

      }, (error) => {
        this.SpinnerService.hide();
      })
    }
  }
  deleteStoreReviewById(ele: any) {
    console.log(ele);
    this.SpinnerService.show();
    this.superAdminService.deleteFoodReviewById(ele).subscribe((res: any) => {
      console.log(res);
      this.element = [];
      this.getStoreReview();

    })

  }
  updateStoreReviewById(data: any) {
    this.SpinnerService.show();
    const status = data.isApproved = !data.isApproved;
    console.log(status);
    const body = {
      isApproved: status
    }

    this.superAdminService.updateFoodReviewStatus(data._id, body).subscribe((res: any) => {
      console.log(res);
      for (var i = 0; i < this.element.length; i++) {
        this.element.forEach((ele: any) => {
          if (data._id === ele._id) {
            ele.isApproved = status
          }
        });
      }
      console.log(this.element);
      this.SpinnerService.hide();
    }, (error) => {
      this.SpinnerService.hide();
    })
  }
  // open mobile slider
  clickEvent(event: any) {

    this.openslider = !this.openslider;
  }

}
