import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { NgxSpinnerService } from 'ngx-spinner';
import { SuperAdminService } from 'src/app/Services/super-admin.service';
import { environment } from 'src/environments/environment';

@Component({
  selector: 'app-carousels',
  templateUrl: './carousels.component.html',
  styleUrls: ['./carousels.component.scss']
})
export class CarouselsComponent implements OnInit {
  openslider: boolean = false;
  toggle: boolean = false;
  StoreForm = false;
  p: number = 1;
  term: any;
  regForm!: FormGroup;
  banerImageUrl: any;
  fileType: any = null;
  filename: any;
  storeList: any = [];
  collections: any = [];
  submitted = false;
  addNewButton: boolean = false;
  carouselId: any;
  imgName: any;
  constructor(private superAdminService: SuperAdminService, private SpinnerService: NgxSpinnerService, private fb: FormBuilder, private httpClient: HttpClient) {
    this.regForm = this.fb.group({
      name: ['', Validators.required],
      image: [''],
      productImg: ['', Validators.required],
      storeId: ['', Validators.required],
      collectionText1: ['', Validators.required],
      collectionText2: ['']

    })
  }

  ngOnInit(): void {
    this.allStores();
    this.SpinnerService.show();
    this.getAllCarouselsImages();

  }

  deleteCarrousel(ele: any) {
    console.log(ele._id);
    var currentUser: any = localStorage.getItem('currentUser');
    const currentUser1 = JSON.parse(currentUser);
    var token = currentUser1.token;
    const headers = new HttpHeaders({ 'Authorization': token });
    this.httpClient.delete(environment.BASE_URL + "/deleteHomeCarouselImage/" + ele._id, { headers: headers }).subscribe((res: any) => {
      console.log(res);
      this.getAllCarouselsImages();
    }, (error) => {
      console.log(error);

    })
  }
  getAllCarouselsImages() {
    var currentUser: any = localStorage.getItem('currentUser');
    const currentUser1 = JSON.parse(currentUser);
    var token = currentUser1.token;
    const headers = new HttpHeaders({ 'Authorization': token });
    this.httpClient.get(environment.BASE_URL + "/fetchHomeCarouselImage", { headers: headers }).subscribe((res: any) => {

      this.collections = res.message;
      this.collections.forEach((ele: any) => {
        if (ele.collectionImage[0] != null) {
          ele.collectionImage[0] = "https://s3.wasabisys.com/valart/" + ele.collectionImage[0]

        }
      });
      this.SpinnerService.hide();
      console.log(this.collections);
    }, (error) => {
      this.SpinnerService.hide();
    })
  }
  allStores() {
    this.superAdminService.allStore().subscribe((res: any) => {
      for (let i of res.data) {
        this.storeList.push({
          storeName: i.name,
          storeId: i._id
        })
      }
      // console.log(this.storeList);
      this.SpinnerService.hide();
    }, (error) => {
      this.SpinnerService.hide();
    })
  }
  showImagePreview(event: any) {
    const file = (event.target).files[0];
    this.filename = file;
    console.log("filename:" + this.filename.name);

    this.regForm.controls.productImg.setValue(file, { emitModelToViewChange: false });
    console.log(this.regForm.controls.productImg.value);
    this.fileType = (event.target).files[0].type;
    const reader = new FileReader();
    reader.onload = () => {
      this.banerImageUrl = reader.result as string;
    }
    reader.readAsDataURL(file)

    //  this.regForm.controls.productImg.setValue(file, { emitModelToViewChange: false });

  }
  clearFileType() {
    this.filename = null;
    this.fileType = null;
    this.banerImageUrl = "";
  }
  addNew() {
    this.carouselId = "";
    this.StoreForm = true;
    this.addNewButton = true;
    this.imgName = "";
    this.banerImageUrl = null;
  }
  edit(ele: any) {
    console.log(ele);
    this.carouselId = ele._id;
    this.banerImageUrl = ele.collectionImage[0];
    this.regForm.controls.name.setValue(ele.collectionName);
    this.regForm.controls.collectionText1.setValue(ele.collectionText1);
    this.regForm.controls.collectionText2.setValue(ele.collectionText2);
    this.regForm.controls.storeId.setValue(ele.collectionStoreId);
    this.collections.forEach((element: any) => {
      if (element._id === ele._id) {
        const imageName = element.collectionImage[0].split("valart/", 2);
        this.imgName = imageName[1];
        console.log(this.imgName);

      }

    });


    this.StoreForm = true;
    this.addNewButton = false;
  }
  Update() {
    this.addNewButton = false;
    this.submitted = true;

    // if (this.regForm.invalid) {
    //   console.log("invalid");
    //   console.log(this.regForm.value);
    //   return;
    // }
    if (this.regForm.controls.name.invalid
      || this.regForm.controls.storeId.invalid
      || this.regForm.controls.collectionText1.invalid) {
      console.log("invalid");
      console.log(this.regForm.value);
      return
    }

    console.log(this.regForm.value);
    this.SpinnerService.show();
    var currentUser: any = localStorage.getItem('currentUser');
    const currentUser1 = JSON.parse(currentUser);
    var token = currentUser1.token;
    const headers = new HttpHeaders({ 'Authorization': token });
    const formData = new FormData();
    formData.append("collectionName", this.regForm.controls.name.value);
    formData.append("collectionStoreId", this.regForm.controls.storeId.value)
    formData.append("collectionText1", this.regForm.controls.collectionText1.value);
    formData.append("collectionText2", this.regForm.controls.collectionText2.value);
    formData.append("carouselImages", this.regForm.controls.productImg.value);
    // // 

    this.httpClient.put(environment.BASE_URL + "/updateHomeCarouselImage/" + this.carouselId, formData, { headers: headers }).subscribe((res: any) => {
      console.log(res);
      this.clearFileType();
      //// 
      this.StoreForm = false
      this.submitted = false;
      this.regForm.reset();
      this.getAllCarouselsImages();
      this.SpinnerService.hide();
    }, (error) => {
      this.submitted = false;
      console.log(error);
      this.SpinnerService.hide();
    })

  }
  addCarousels() {
    this.submitted = true;
    if (this.regForm.invalid) {
      console.log("invalid");
      console.log(this.regForm.value);
      return;
    }
    console.log(this.regForm.value);
    this.SpinnerService.show();
    var currentUser: any = localStorage.getItem('currentUser');
    const currentUser1 = JSON.parse(currentUser);
    var token = currentUser1.token;
    const headers = new HttpHeaders({ 'Authorization': token });
    const formData = new FormData();
    formData.append("collectionName", this.regForm.controls.name.value);
    formData.append("collectionStoreId", this.regForm.controls.storeId.value);
    formData.append("collectionText1", this.regForm.controls.collectionText1.value);
    formData.append("collectionText2", this.regForm.controls.collectionText2.value);
    formData.append("carouselImages", this.regForm.controls.productImg.value);

    // // 
    this.httpClient.post(environment.BASE_URL + "/addHomeCarouselImage", formData, { headers: headers }).subscribe((res: any) => {
      console.log(res);
      this.clearFileType();
      // // 
      this.StoreForm = false
      this.submitted = false;
      this.addNewButton = false;
      this.regForm.reset();
      this.getAllCarouselsImages();
      this.SpinnerService.hide();
    }, (error) => {
      this.submitted = false;
      console.log(error);
      this.SpinnerService.hide();
    })

  }
  clickEvent(event: any) {

    this.openslider = !this.openslider;
  }
  cancle() {
    this.StoreForm = false;
    this.regForm.reset();
  }
  get f() { return this.regForm.controls; }
}
