import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { CategoryListComponent } from './category-list/category-list.component';
import { CategoryComponent } from './category/category.component';
import { Category1Component } from './category1/category1.component';
import { ManagerComponent } from './manager/manager.component';
import { MarginAddComponent } from './margin/margin-add/margin-add.component';
import { MarginComponent } from './margin/margin.component';
import { FoodReviewDetailsComponent } from './review/food-review-details/food-review-details.component';
import { ProductReviewDetailsComponent } from './review/product-review-details/product-review-details.component';
import { ServiceReviewDetailsComponent } from './review/service-review-details/service-review-details.component';
import { StoreReviewDetailsComponent } from './review/store-review-details/store-review-details.component';
import { StoreDetailsComponent } from './store-details/store-details.component';
import { StoreListComponent } from './store-list/store-list.component';
import { StoreVerifyComponent } from './store-verify/store-verify.component';
import { AddTaxComponent } from './tax/add-tax/add-tax.component';
import { TaxComponent } from './tax/tax.component';
import { UserDetailsComponent } from './user-details/user-details.component';
import { UserVerifyComponent } from './user-verify/user-verify.component';
import { ListComponent } from './payment/list/list.component';
import { DetailsComponent } from './payment/details/details.component';
import { OrderListComponent } from './order/order-list/order-list.component';
import { OrderDetailsComponent } from './order/order-details/order-details.component';
import { CarouselsComponent } from './carousels/carousels.component';






const routes: Routes = [
    {
        path: '',
        redirectTo: 'store-list',
        pathMatch: 'full'
    },
    { path: "store-list", component: StoreListComponent },
    { path: "store-detail/:id", component: StoreDetailsComponent },
    { path: "store-verify", component: StoreVerifyComponent },
    { path: "user-verify", component: UserVerifyComponent },
    { path: "manager", component: ManagerComponent },
    { path: "user-details/:id", component: UserDetailsComponent },
    { path: "category-list", component: CategoryListComponent },
    { path: "category", component: CategoryComponent },
    { path: "categoryView/:id", component: CategoryComponent },
    // { path: "category", component: Category1Component },
    // { path: "categoryView/:id", component: Category1Component },
    { path: "tax", component: TaxComponent },
    { path: "add-tax", component: AddTaxComponent },
    { path: "add-tax/:id", component: AddTaxComponent },
    { path: "store-review-details/:id", component: StoreReviewDetailsComponent },
    { path: "food-review-details/:id", component: FoodReviewDetailsComponent },
    { path: "product-review-details/:id", component: ProductReviewDetailsComponent },
    { path: "service-review-details/:id", component: ServiceReviewDetailsComponent },
    { path: "margin-list", component: MarginComponent },
    { path: "add-margine", component: MarginAddComponent },
    { path: "add-margine/:id", component: MarginAddComponent },
    { path: "payment-list", component: ListComponent },
    { path: "payment-detail/:id", component: DetailsComponent },
    { path: "super-admin-order-list", component: OrderListComponent },
    { path: "super-admin-order-detail/:id", component: OrderDetailsComponent },
    { path: "carouselImg", component: CarouselsComponent },
    {
        path: '**',
        redirectTo: 'store-list',
        pathMatch: 'full'
    }
];

@NgModule({
    imports: [RouterModule.forChild(routes)],
    exports: [RouterModule]
})
export class SuperAdminRoutingModule { }
