import { DatePipe } from '@angular/common';
import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { NgxSpinnerService } from 'ngx-spinner';
import { AdminService } from 'src/app/Services/admin.service';
import { DataService } from 'src/app/Services/data.service';
import {FormControl} from '@angular/forms';
import {Observable} from 'rxjs';
import {startWith, map} from 'rxjs/operators';

@Component({
  selector: 'app-order-list',
  templateUrl: './order-list.component.html',
  styleUrls: ['./order-list.component.scss']
})
export class OrderListComponent implements OnInit {
  control = new FormControl();
  store: string[] = ['Ganesh Genral Store', 'Jai Ganesh Fresh Fruits', 'Raj Chianis', 'Hari Om Juice Center'];
  filteredStores!: Observable<string[]>;

  Orders: any = [];
  p: number = 1;
  constructor(private adminService: AdminService,
    private router: Router, private date: DatePipe, private SpinnerService: NgxSpinnerService) { }
  openslider: boolean = false;
  ngOnInit(): void {
    this.getOrderByStoreId();
    this.filteredStores = this.control.valueChanges.pipe(
      startWith(''),
      map(value => this._filter(value))
    );
  }
  orderDetails(ele: any) {
    //[routerLink]="'/superAdmin/super-admin-order-detail'"
    this.SpinnerService.show();
    this.router.navigateByUrl('/superAdmin/super-admin-order-detail/' + ele);
  }
  getOrderByStoreId() {
    this.SpinnerService.show();
    this.adminService.getOrderByStoreId().subscribe((res: any) => {
      console.log(res);
      this.Orders = res.results
      console.log(this.Orders)

      for (var i = 0; i < this.Orders.length; i++) {

        this.Orders[i].orderDate = this.date.transform(this.Orders[i].orderDate, 'dd-MM-yyyy');

        if (this.Orders[i].product.length != 0) {

          this.Orders[i].product.forEach((element: any) => {
            for (var j = 0; j < element.productId.productImageId.pImage.length; j++) {
              element.productId.productImageId.pImage[j] = "https://s3.wasabisys.com/valart/".concat(element.productId.productImageId.pImage[j]);
            }

            if (!element.isCanceled) {
              //   // ;
              this.Orders[i].shiprocketbtn = true;
            }
          });
        }
        if (this.Orders[i].food.length != 0) {
          this.Orders[i].food.forEach((element: any) => {

            for (var j = 0; j < element.foodId.foodImageId.thumbnailImage.length; j++) {
              element.foodId.foodImageId.thumbnailImage[j] = "https://s3.wasabisys.com/valart/".concat(element.foodId.foodImageId.thumbnailImage[j]);
            }
            if (!element.isCanceled) {
              
              this.Orders[i].shiprocketbtn = true;
            }
          });
        }

      }
      this.SpinnerService.hide();
      console.log(this.Orders)
    }, (error) => {
      this.SpinnerService.hide();
      console.log(error);

    })
  }
  clickEvent() {
    this.openslider = !this.openslider;
  }

  private _filter(value: string): string[] {
    const filterValue = this._normalizeValue(value);
    return this.store.filter(store => this._normalizeValue(store).includes(filterValue));
  }

  private _normalizeValue(value: string): string {
    return value.toLowerCase().replace(/\s/g, '');
  }


}
