import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';

import { SuperAdminService } from '../../../Services/super-admin.service';
import { getTestBed } from '@angular/core/testing';
import { NgxSpinnerService } from 'ngx-spinner';
import {
  FormBuilder,
  FormGroup,
  FormControl,
  Validators,
} from '@angular/forms';

@Component({
  selector: 'app-details',
  templateUrl: './details.component.html',
  styleUrls: ['./details.component.scss'],
})
export class DetailsComponent implements OnInit {
  constructor(
    private actRoute: ActivatedRoute,
    private service: SuperAdminService,
    private fb: FormBuilder,
    private SpinnerService: NgxSpinnerService
  ) {}
  openslider: boolean = false;
  updateSettelment: boolean = false;
  confirmationPopup: boolean = false;
  productDetail: any = [];
  productId: any;
  productImgUrl: any = [];
  productimgURL: any = null;
  productimgs: any = [];
  fileData: any;
  public paymentForm!: FormGroup;
  ngOnInit(): void {
    this.productId = this.actRoute.snapshot.paramMap.get('id');
    console.log('Id', this.productId);
    this.formInit();
    this.SpinnerService.show();
    this.getPaymentDetail(this.productId);
  }

  formInit() {
    this.paymentForm = this.fb.group({
      comment: new FormControl('', Validators.compose([])),
      paymentFile: new FormControl('', Validators.compose([])),
      paymentReleased: new FormControl('', Validators.compose([])),
    });
  }

  showPic(event: any) {
    this.fileData = null;
    let file = event.target.files[0];
    console.log('file:' + file);
    this.fileData = file;
    const reader = new FileReader();
    reader.onload = () => {
      this.productimgURL = reader.result as string;
      // this.productImgUrl.push(this.productimgURL);
    };
    reader.readAsDataURL(file);
  }

  openPoup(Data: any) {
    console.log('checked', Data.target.checked);
    this.updateSettelment = Data.target.checked;
    this.confirmationPopup = true;
    this.paymentForm.controls.paymentReleased.setValue(Data.target.checked);
  }
  onCheck() {
    this.confirmationPopup = false;
    // this.updateSettelment = false ;
    // this.paymentForm.controls.paymentFile.setValue(this.productimgs, {
    //   emitModelToViewChange: false,
    // });

    const formData = new FormData();
    formData.append('paymentFile', this.fileData);
    formData.append('comment', this.paymentForm.controls.comment.value);
    formData.append(
      'paymentReleased',
      this.paymentForm.controls.paymentReleased.value
    );
    // this.paymentForm.value.paymentFile = { name: 'file1.jpg', type: '.png' };
    console.log('Form Data', formData);

    this.service
      .updatePaymentStatus(this.productId, formData)
      .subscribe((res: any) => {
        console.log('res', res);
        this.getPaymentDetail(this.productId);
      });
  }
  closeModal() {
    this.confirmationPopup = false;
    this.updateSettelment = this.productDetail.paymentReleased.status;
  }
  getPaymentDetail(id: any) {
    this.service.paymentDetailsById(id).subscribe((resp: any) => {
      this.SpinnerService.hide();
      this.productDetail = resp.data;
      console.log('data', this.productDetail);

      this.updateSettelment = this.productDetail.paymentReleased.status;
      if (this.productDetail.food.length != 0) {
        this.productDetail.product = this.productDetail.food;
      }

      let result = 0;
      let comission = 0;
      let individualTaxAmount = 0;
      let totalDeliveryCharge = 0;
      let gstOnBasePrice = 0;
      this.productDetail.sellingPrice = 0;
      this.productDetail.product.forEach((item: any) => {
        let outgoingAmt = 0;
        if (item.hasOwnProperty('productId')) {
          result =
            item.productSellingPrice - parseInt(item.productId.basePrice);

          individualTaxAmount =
            (item.productSellingPrice / 100) * this.productDetail.taxRate;
          item.taxAmount = individualTaxAmount.toFixed(2);
          item.comissionCharge = result;

          comission += result;
          if (item.shiprocket_delivery_charges) {
            if (this.productDetail.product.length > 1) {
              totalDeliveryCharge += item.shiprocket_delivery_charges;
            } else {
              totalDeliveryCharge = item.shiprocket_delivery_charges;
            }
          } else {
            item.shiprocket_delivery_charges = 0;
          }
          if (item.isCanceled == true) {
            item.cancelledAmount =
              individualTaxAmount +
              item.productSellingPrice +
              item.shiprocket_delivery_charges;
          }
          if (this.productDetail.storeId.gstNumber) {
            gstOnBasePrice =
              (parseInt(item.productId.basePrice) / 100) *
              this.productDetail.taxRate;
            item.gstOnBasePrice = gstOnBasePrice.toFixed(2);
          } else {
            item.gstOnBasePrice = 0;
          }
          item.payableToSeller =
            gstOnBasePrice + parseInt(item.productId.basePrice);
          outgoingAmt =
            item.productSellingPrice -
            (item.productSellingPrice - parseInt(item.productId.basePrice));
          item.outgoingAmount = outgoingAmt;
        }
        //food Calculations
        else {
          result = item.foodPrice - item.foodSellingPrice;
          individualTaxAmount =
            (item.foodSellingPrice / 100) * this.productDetail.taxRate;
          item.taxAmount = individualTaxAmount.toFixed(2);
          if (item.shiprocket_delivery_charges) {
            if (this.productDetail.product.length > 1) {
              totalDeliveryCharge += item.shiprocket_delivery_charges;
            } else {
              totalDeliveryCharge = item.shiprocket_delivery_charges;
            }
          } else {
            item.shiprocket_delivery_charges = 0;
          }
          if (item.isCanceled == true) {
            item.cancelledAmount =
              individualTaxAmount +
              item.foodSellingPrice +
              item.shiprocket_delivery_charges;
          }
          if (this.productDetail.storeId.gstNumber) {
            gstOnBasePrice =
              (parseInt(item.foodPrice) / 100) * this.productDetail.taxRate;
            item.gstOnBasePrice = gstOnBasePrice.toFixed(2);
          } else {
            item.gstOnBasePrice = 0;
          }
          item.payableToSeller =
            gstOnBasePrice + parseInt(item.productId.basePrice);

          item.comissionCharge = result;
          outgoingAmt =
            item.foodSellingPrice - (item.foodPrice - item.foodSellingPrice);
          item.outgoingAmount = outgoingAmt;
        }

        this.productDetail.totalDeliveryCharge = totalDeliveryCharge.toFixed(2);
      });
      let res: any;
      for (let i = 0; i < this.productDetail.product.length; i++) {
        if (this.productDetail.product.length > 1) {
          res =
            this.productDetail.product[i].shiprocket_delivery_charges +
            this.productDetail.product[this.productDetail.product.length - 1]
              .shiprocket_delivery_charges +
            (this.productDetail.product[0].outgoingAmount +
              this.productDetail.product[this.productDetail.product.length - 1]
                .outgoingAmount) +
            (this.productDetail.product[0].comissionCharge +
              this.productDetail.product[this.productDetail.product.length - 1]
                .comissionCharge);
        } else {
          res =
            this.productDetail.product[i].shiprocket_delivery_charges +
            this.productDetail.product[i].outgoingAmount +
            this.productDetail.product[i].comissionCharge;
        }
      }

      console.log('comission', this.productDetail);
      this.productDetail.totalComissionCharges = comission.toFixed(2);
      this.productDetail.grandTotalAmount = res + this.productDetail.taxAmount;
      this.productDetail.grandTotalAmount =
        this.productDetail.grandTotalAmount.toFixed(2);

      // this.productDetail.comissionCharges = this.productDetail.comissionCharges.toFixed(2)

      // this.productDetail.shiprocket_delivery_charges = res ;
      // console.log("Response", this.productDetail);
    });

    //
  }

  clickEvent() {
    this.openslider = !this.openslider;
  }
}
