import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { NgxSpinnerService } from 'ngx-spinner';
import { SuperAdminService } from 'src/app/Services/super-admin.service';


@Component({
  selector: 'app-user-verify',
  templateUrl: './user-verify.component.html',
  styleUrls: ['./user-verify.component.scss']
})
export class UserVerifyComponent implements OnInit {
  openslider: boolean = false;
  storeType: any;
  element: any = [];
  p: number = 1;
  term: any;
  headElements = ['Name', 'Mobile', 'Address', "Pin code ", "Byzcode", "Action", "View"];
  alertMsg: any;
  constructor(private superService: SuperAdminService, private router: Router, private SpinnerService: NgxSpinnerService) { }

  ngOnInit(): void {
    this.SpinnerService.show();
    this.getUser();
  }
  activeUser(ele: any) {
    console.log(ele._id);
    this.SpinnerService.show();
    this.superService.accountActive(ele._id).subscribe((res: any) => {
      console.log(res);
      this.element = [];
      this.getUser();
      this.alertMsg = res.message;
    })

  }
  handleError() {
    this.alertMsg = null;
  }
  getUserDetails(ele: any) {
    const id = ele._id;
    this.router.navigateByUrl('/superAdmin/user-details/' + id)
  }
  getUser() {
    this.superService.getAllUser().subscribe((res: any) => {
      console.log(res);

      for (let i of res.data) {
        this.element.push({
          fullName: i.fullName,
          gender: i.gender,
          mobile: i.mobile,
          city: i.city,
          activaed: "",
          activationSatus: i.activationSatus,
          _id: i._id,
          pincode: i.pincode,
          code: i.code
        })
      }
      this.element.forEach((ele: { activationSatus: any; activaed: string; }) => {
        if (!ele.activationSatus === true) {
          ele.activaed = "Activate"
        }
        if (!ele.activationSatus === false) {
          ele.activaed = "Deactivate"
        }
      });
      console.log(this.element);
      this.SpinnerService.hide();
    })
  }

  // clear search
  handleClear(){
    this.term = '';
  }
  
  clickEvent(event: any) {

    this.openslider = !this.openslider;
  }
}
