import { Component, OnDestroy, OnInit, ViewChild } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { NgxSpinnerService } from 'ngx-spinner';
import { DataService } from 'src/app/Services/data.service';
import { SuperAdminService } from 'src/app/Services/super-admin.service';
import { MatDialog } from '@angular/material/dialog';
import { AdminService } from 'src/app/Services/admin.service';

@Component({
  selector: 'app-store-details',
  templateUrl: './store-details.component.html',
  styleUrls: ['./store-details.component.scss']
})
export class StoreDetailsComponent implements OnInit, OnDestroy {
  @ViewChild("openimg", { static: false }) openimg: any;
  openslider: boolean = false;
  storeDetails: any = [];
  storeId: any;
  DocumentIMG1: any;
  DocumentIMG2: any;
  chequebookpopup: boolean = false;
  adharImages: any = [];
  panImages: any = [];
  DocImg: any = [];
  chekBookImage: any;
  fevrate: number = 0;
  storeLogo: any;
  banerImg: any;
  bankCheck: any;
  constructor(private dialog: MatDialog, private superAdminService: SuperAdminService,
    private adminService: AdminService, private dataService: DataService, private activatedRoute: ActivatedRoute, private SpinnerService: NgxSpinnerService) { }

  ngOnInit(): void {
    //  this.SpinnerService.show();
    this.getData();
    this.getPetrons();
  }
  getPetrons() {
    this.adminService.getPetron(this.storeId).subscribe((res: any) => {
      if (res.data) {
        this.fevrate = 0 + res.data.length;

      } if (!res.data) {
        this.fevrate = 0;
      }
      console.log(this.fevrate);

    })
  }
  getData() {
    this.storeId = this.activatedRoute.snapshot.paramMap.get('id');
    this.superAdminService.getStoreById(this.storeId).subscribe((res: any) => {
      this.storeDetails = res;
      console.log(res);

      this.adharImages = res[0].storeImagesId.aadharCardImages;


      //  console.log(this.adharImages);
      this.panImages = res[0].storeImagesId.panCardImages;
      //  console.log(this.panImages);
      if (this.storeDetails[0].storeImagesId.storeLogo) {
        this.storeLogo = "https://s3.wasabisys.com/valart/" + this.storeDetails[0].storeImagesId.storeLogo;
      }


      if (!this.storeDetails[0].storeImagesId.storeLogo) {
        this.storeLogo = "http://localhost:4200/assets/images/noImg.png"
      }



      if (this.storeDetails[0].storeImagesId.storeImages[0]) {
        this.banerImg = "https://s3.wasabisys.com/valart/" + this.storeDetails[0].storeImagesId.storeImages[0]
      }
      if (!this.storeDetails[0].storeImagesId.storeImages[0]) {
        this.banerImg = "http://localhost:4200/assets/images/noImg.png";
      }
      if (this.storeDetails[0].storeImagesId.cancelledChequeImage == "" || "undefined" || null) {
        this.chekBookImage = "https://s3.wasabisys.com/valart/" + this.storeDetails[0].storeImagesId.cancelledChequeImage
      }
      if (this.storeDetails[0].storeImagesId.cancelledChequeImage != null) {
        this.banerImg = "http://localhost:4200/assets/images/noImg.png";
      }
      // this.storeDetails.forEach((ele: any) => {
      //   // ele.storeImagesId.storeLogo = "https://s3.wasabisys.com/valart/" + ele.storeImagesId.storeLogo,
      //   //   ele.storeImagesId.storeImages[0] = "https://s3.wasabisys.com/valart/" + ele.storeImagesId.storeImages[0],

      //   ele.storeImagesId.cancelledChequeImage = "https://s3.wasabisys.com/valart/" + ele.storeImagesId.cancelledChequeImage
      // });
      // console.log(this.storeDetails);

      this.SpinnerService.hide();
      this.storeDetails.forEach((ele: { verified: boolean; store: string; }) => {
        if (ele.verified == false || typeof ele.verified === 'undefined') {
          ele.store = "Activate Store"
        }
        if (ele.verified == true) {
          ele.store = "Deactivate store"
        }
      });
      console.log(this.storeDetails);


    }, (error) => {
      this.SpinnerService.hide();
    })
    // https://s3.wasabisys.com/valart/


  }
  activeStore(ele: any) {
    console.log(ele);
    this.SpinnerService.show();
    this.superAdminService.storeActive(ele).subscribe((res: any) => {
      console.log(res);
      this.storeDetails = [];
      this.getData();
    })
  }

  // open aadhaar
  AdharImg() {
    //  this.storeDetails
    this.chequebookpopup = false,
      // this.DocumentIMG1 = "assets/images/addhar.jfif",
      // this.DocumentIMG2 = "assets/images/addhar2.png",
      this.DocImg = this.adharImages;
    this.dialog.open(this.openimg, {
      panelClass: ['viewdocument']
    });
  }
  // open Pan
  panImg() {
    this.chequebookpopup = false,
      // this.DocumentIMG1 = "assets/images/pan.webp",
      // this.DocumentIMG2 = "assets/images/pan.webp",
      this.DocImg = this.panImages
    this.dialog.open(this.openimg, {
      panelClass: ['viewdocument']
    });
  }

  // open cheque book
  chequeImg() {
    this.chequebookpopup = true,
      this.DocumentIMG1 = "assets/images/pan.webp",
      this.DocumentIMG2 = "assets/images/pan.webp",
      this.dialog.open(this.openimg, {
        panelClass: ['viewdocument', 'checkbook']
      });
  }
  // open slider
  clickEvent(event: any) {

    this.openslider = !this.openslider;
  }
  ngOnDestroy(): void {
    this.storeDetails = [];
    this.storeId = "";
  }

}
