import { Component, OnInit } from '@angular/core';
import { FormGroup, FormControl, FormBuilder, Validators } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { SuperAdminService } from 'src/app/Services/super-admin.service';

@Component({
  selector: 'app-category1',
  templateUrl: './category1.component.html',
  styleUrls: ['./category1.component.scss']
})
export class Category1Component implements OnInit {

  storeType: any;
  element: any = [];
  allSalestrip: any = [];
  updteButton = false;
  regForm!: FormGroup;
  productimgURL: any = null;
  id: any;
  openslider: boolean = false;
  productImgUrl: any = [];
  categoryType = ["Product", "Services", "Food"]
  productimgs: any = [];
  userId: any;
  updateForm = false;


  dropdownList: any = [];
  selectedItems: any = [];
  //  dropdownSettings: IDropdownSettings = {};
  dropdownSettings = {};
  demoTax: any = [];
  constructor(private formBuilder: FormBuilder,
    private activatedRoute: ActivatedRoute,
    private superAdminService: SuperAdminService,
    private router: Router) {


  }

  ngOnInit(): void {
    this.dropdownSettings = {
      singleSelection: false,
      idField: 'item_id',
      textField: 'item_text',
      selectAllText: 'Select All',
      unSelectAllText: 'UnSelect All',
      itemsShowLimit: 3,
      allowSearchFilter: true
    };
    this.dropdownList = [
      { item_id: 1, item_text: 'Mumbai' },
      { item_id: 2, item_text: 'Bangaluru' },
      { item_id: 3, item_text: 'Pune' },
      { item_id: 4, item_text: 'Navsari' },
      { item_id: 5, item_text: 'New Delhi' }
    ]
    this.selectedItems = [
      { item_id: 1, item_text: 'Mumbai' },
      { item_id: 2, item_text: 'Bangaluru' },
      { item_id: 3, item_text: 'Pune' },

    ]
    this.regForm = this.formBuilder.group({
      storeType: ['', Validators.required],
      name: ['', Validators.required],
      image: [''],
      productimgs: [''],
      tax: [[], Validators.required]

    })


    //  this.getTax();
    this.userId = this.activatedRoute.snapshot.paramMap.get('id');
    if (this.userId) {
      this.updteButton = true;
      this.superAdminService.getCategoryById(this.userId).subscribe((res: any) => {
        console.log(res);
        this.regForm.patchValue({
          name: res.data.name,
          storeType: res.data.storeType,
        });
        this.productimgURL = "https://valart.s3.us-east-1.wasabisys.com/" + res.data.image;
        const data: any = [];
        for (let i of res.data.taxId) {
          data.push({
            id: i._id,
            itemName: i.name
          })
        }
        console.log(res.data.taxId);

        //   this.selectedItems = data;
        console.log(this.selectedItems);
        var t2 = [
          { item_id: 1, item_text: 'Mumbai' },
          { item_id: 2, item_text: 'Bangaluru' },
          { item_id: 3, item_text: 'Pune' },

        ]
        this.regForm.patchValue({
          tax: res.data.taxId._id
        })

      })
    }


  }
  onItemSelect(item: any) {
    console.log(this.selectedItems);
  }
  onSelectAll(items: any) {
    console.log(this.selectedItems);
  }
  OnItemDeSelect(ele: any) {
    console.log(this.selectedItems);
  }
  onDeSelectAll(ele: any) {
    console.log(this.selectedItems);
  }
  getTax() {
    this.superAdminService.getAllTax().subscribe((res: any) => {
      console.log(res.data);
      const data: any = [];
      for (let i of res.data) {
        data.push({
          id: i._id,
          itemName: i.name
        })
      }
      //  this.dropdownList = data;
      console.log(this.dropdownList);

    })
  }
  updateCategoty() {

    var tax1 = this.selectedItems.map((x: any) => x.id);
    console.log(tax1);
    const tax = JSON.stringify(tax1);


    if (this.regForm.invalid) {
      console.log("form is invalid");

      return
    }


    let formData: any = new FormData();
    formData.append('name', this.regForm.value.name);
    formData.append('storeType', this.regForm.value.storeType);
    formData.append('image', this.regForm.value.productimgs);
    //formData.append('taxId', this.regForm.value.tax);
    formData.append('taxId', "60e54bbef7f261068ffa1ae6");
    console.log(this.demoTax);


    for (var value of formData.values()) {
      console.log(value);
    }

    this.superAdminService.updateCategory(formData, this.userId).subscribe((res: any) => {
      this.regForm.reset();
      this.productimgURL = "";
      console.log(res);
      this.router.navigateByUrl("/superAdmin/category-list")

    })

  }
  cancle() {
    this.regForm.reset();
    this.productimgURL = "";
    this.router.navigateByUrl("/superAdmin/category-list")
  }
  clickEvent(event: any) {

    this.openslider = !this.openslider;
  }
  addCategory() {

    if (this.regForm.invalid) {
      console.log("invalid");

      return
    }
    const tax = this.selectedItems.map((x: any) => x.id);
    console.log(tax);

    let formData: any = new FormData();
    formData.append('name', this.regForm.value.name);
    formData.append('storeType', this.regForm.value.storeType);
    formData.append('image', this.regForm.value.productimgs);
    //formData.append("taxId", this.tax);
    console.log(formData);

    this.superAdminService.addCategory(formData).subscribe((res: any) => {
      this.regForm.reset();
      this.productimgURL = "";
      console.log(res);
      this.router.navigateByUrl("/superAdmin/category-list")

    })

  }
  showPic(event: any) {
    // this.imageChangedEvent=event;

    let file = (event.target).files[0];
    console.log("file:" + file);
    this.regForm.controls.productimgs.setValue(file, { emitModelToViewChange: false });
    const reader = new FileReader();
    reader.onload = () => {
      this.productimgURL = reader.result as string;
      // this.productImgUrl.push(this.productimgURL);
    }
    // this.productimgs.push(file);

    reader.readAsDataURL(file);

  }



}
