import { Component, OnInit } from '@angular/core';
import { SuperAdminService } from 'src/app/Services/super-admin.service';


@Component({
  selector: 'app-store-verify',
  templateUrl: './store-verify.component.html',
  styleUrls: ['./store-verify.component.scss']
})
export class StoreVerifyComponent implements OnInit {
  openslider: boolean = false;
  storeType: any;
  element: any = [];
  p: number = 1;
  term: any;
  headElements = ['Name', 'Store', 'Mobile', 'Address', "Action"];
  constructor(private superService: SuperAdminService) { }

  ngOnInit(): void {
    this.getUser();
  }
  activeUser(ele: any) {
    console.log(ele._id);
    this.superService.accountActive(ele._id).subscribe((res: any) => {
      console.log(res);
      this.ngOnInit();

    })

  }
  getUser() {
    this.superService.getAllUser().subscribe((res: any) => {

      this.element = res.data
      console.log(this.element);
    })
  }

}
