import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { NgxSpinnerService } from 'ngx-spinner';
import { SuperAdminService } from 'src/app/Services/super-admin.service';

@Component({
  selector: 'app-manager',
  templateUrl: './manager.component.html',
  styleUrls: ['./manager.component.scss']
})
export class ManagerComponent implements OnInit {
  p: number = 1;
  term: any;
  openslider: boolean = false;
  storeType: any;
  element: any = [];
  allSalestrip: any = [];
  headElements = ['Name', "Action"];
  updteButton = false;
  regForm!: FormGroup;
  id: any;
  alertMsg: null;
  constructor(private superAdminService: SuperAdminService, private fb: FormBuilder, private SpinnerService: NgxSpinnerService) { }

  ngOnInit(): void {
    this.regForm = this.fb.group({
      tip: ['', Validators.required],
      _id: ['']
    })
    this.getSalestip();
  }
  edit(element: any) {
    console.log(element);

    this.updteButton = true;
    this.regForm.patchValue({
      tip: element.tip,
      _id: element._id
    })
  }
  cancleUpdate() {
    this.updteButton = false;
    this.regForm.reset();
  }
  cancleAdd() {
    // this.updteButton = true;
    this.regForm.reset();
  }
  deleteSaleStrip(ele: any) {
    this.SpinnerService.show();

    this.superAdminService.deleteSaleStripById(ele._id).subscribe((res: any) => {
      console.log(res);
      this.getSalestip();

    })
  }
  getDetails(id: any) {
    this.superAdminService.getSaleStripById(id).subscribe((res: any) => {
      console.log(res);
      this.alertMsg = res.data.tip;

    })
  }
  handleError() {
    this.alertMsg = null;
  }
  updateForm() {
    this.SpinnerService.show();
    console.log(this.regForm.value);
    const tip1 = this.regForm.get("tip")?.value
    const data = {
      tip: tip1
    }
    const id = this.regForm.get("_id")?.value
    this.superAdminService.updateSalestrip(data, id).subscribe((res: any) => {
      console.log(res);
      this.updteButton = false;
      this.regForm.reset();
      this.getSalestip();

    }, (error) => {
      this.SpinnerService.hide();
    })

  }
  addSalestrip() {
    console.log(this.regForm.value);
    this.SpinnerService.show();
    const tip1 = this.regForm.get("tip")?.value
    const data = {
      tip: tip1
    }
    this.superAdminService.addSaleStrip(data).subscribe((res: any) => {
      console.log(res);
      this.regForm.reset();
      //   this.updteButton = false;
      this.getSalestip();

    })
  }
  getSalestip() {
    this.superAdminService.getAllSalestrip().subscribe((res: any) => {
      this.allSalestrip = res.data;
      this.SpinnerService.hide();
      // console.log(this.allSalestrip);

    }, (error) => {
      this.SpinnerService.hide();
    })
  }
  clickEvent(event: any) {

    this.openslider = !this.openslider;
  }
}
