import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { NgxSpinnerService } from 'ngx-spinner';
import { SuperAdminService } from 'src/app/Services/super-admin.service';

@Component({
  selector: 'app-margin',
  templateUrl: './margin.component.html',
  styleUrls: ['./margin.component.scss']
})
export class MarginComponent implements OnInit {

  p: number = 1;
  term: any;
  element: any = [];
  openslider: boolean = false;
  constructor(private superAdminService: SuperAdminService, private router: Router, private SpinnerService: NgxSpinnerService) { }

  ngOnInit(): void {
    this.SpinnerService.show();
    this.getTax();
  }
  getTax() {
    this.superAdminService.getAllMargin().subscribe((res: any) => {
      console.log(res);
      if (res.success === true) {
        this.element = res.data;
      }
      this.SpinnerService.hide();
    }, (error) => {
      this.SpinnerService.hide();
    })
  }

  addNew() {
    this.router.navigateByUrl("/superAdmin/add-margine");
    // add-tax
  }
  edit(ele: any) {
    console.log(ele._id);
    this.router.navigateByUrl("/superAdmin/add-margine/" + ele._id);
  }
  deleteTax(ele: any) {
    this.SpinnerService.show();
    this.superAdminService.deleteMarginById(ele._id).subscribe((res: any) => {
      console.log(res);
      this.element = [];
      this.getTax();
    })
  }

  // clear search
  handleClear(){
    this.term = '';
  }
  clickEvent(event: any) {

    this.openslider = !this.openslider;
  }
}
