import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { NgxSpinnerService } from 'ngx-spinner';
import { SuperAdminService } from 'src/app/Services/super-admin.service';

@Component({
  selector: 'app-add-tax',
  templateUrl: './add-tax.component.html',
  styleUrls: ['./add-tax.component.scss']
})
export class AddTaxComponent implements OnInit {
  regForm!: FormGroup;
  updteButton = false;
  openslider: boolean = false;
  taxId: any;
  submitted = false;
  alertMsg: null;
  constructor(private formBuildr: FormBuilder,
    private activatedRoute: ActivatedRoute,
    private superAdminService: SuperAdminService,
    private router: Router,
    private SpinnerService: NgxSpinnerService) { }

  ngOnInit(): void {
    this.regForm = this.formBuildr.group({
      name: ["", Validators.required],
      rate: ["", [Validators.required, Validators.maxLength(3)]]
    })

    this.SpinnerService.show();
    this.getTaxData();
  }
  stripText(event: any) {
    const seperator = '^([0-9])';
    const maskSeperator = new RegExp(seperator, 'g');
    let result = maskSeperator.test(event.key); return result;
  }
  getTaxData() {
    this.taxId = this.activatedRoute.snapshot.paramMap.get('id');
    if (this.taxId) {
      this.updteButton = true;
      this.superAdminService.getTaxById(this.taxId).subscribe((res: any) => {
        // console.log(res);
        this.regForm.patchValue({
          name: res.data.name,
          rate: res.data.rate
        })
        this.SpinnerService.hide();
      }, (error) => {
        this.SpinnerService.hide();
      })
    }
    this.SpinnerService.hide();
  }
  get f() {
    return this.regForm.controls;
  }
  clickEvent(event: any) {

    this.openslider = !this.openslider;
  }

  updateTax() {
    this.submitted = true;
    this.SpinnerService.show();
    const postData = {
      name: this.regForm.get("name")?.value,
      rate: this.regForm.get("rate")?.value,
    }
    this.superAdminService.updateTaxById(this.taxId, postData).subscribe((res: any) => {
      this.submitted = false;
      this.SpinnerService.hide();
      this.alertMsg = res.message;

      this.router.navigateByUrl("/superAdmin/tax")
    }, (error) => {
      this.SpinnerService.hide();
    })
  }
  addTax() {
    this.submitted = true;
    const postData = {
      name: this.regForm.get("name")?.value,
      rate: this.regForm.get("rate")?.value,
    }
    this.SpinnerService.show();
    this.superAdminService.addTax(postData).subscribe((res: any) => {
      this.submitted = false;
      this.regForm.reset();
      this.SpinnerService.hide();
      this.router.navigateByUrl("/superAdmin/tax")
      this.alertMsg = res.message;

    }, (error) => {
      this.SpinnerService.hide();
    })

  }
  handleError() {
    this.alertMsg = null;
  }

  cancle() {
    this.regForm.reset();
    this.submitted = false;
    this.router.navigateByUrl("/superAdmin/tax");

  }
}
