  amountCalculation(data: any) {
    data.forEach((element: any, eleIndex: any, elements: any) => {
      if (element.food.length != 0) {
        element.product = element.food;
      }
      // main
      let totalCancelPrice = 0;
      let totalOutgoingAmount = 0;
      let totalBasePrice = 0;
      let totalDeliveryCharge = 0;
      let totalComissionAmount = 0;
      element.product.forEach((item: any) => {
        totalDeliveryCharge += item?.shiprocket_delivery_charges || 0;
        // products
        if (item.productId) {
          totalComissionAmount +=
            item.productSellingPrice -
            (item.productId.basePrice
              ? parseInt(item?.productId?.basePrice)
              : parseInt(item.productPrice));
          if (item.isCanceled) {
            totalCancelPrice += this.getTotalSellingPrice(
              item,
              element.taxRate,
              false
            );
          } else {
            totalOutgoingAmount += this.getTotalSellingPrice(
              item,
              element.taxRate,
              false
            );
          }
          totalBasePrice += this.getTotalBasePrice(
            item,
            element.taxRate,
            false
          );
        } else {
          totalComissionAmount += item.foodSellingPrice - item.foodPrice;
          if (item.isCanceled) {
            totalCancelPrice += this.getTotalSellingPrice(
              item,
              element.taxRate,
              true
            );
          } else {
            totalOutgoingAmount += this.getTotalSellingPrice(
              item,
              element.taxRate,
              true
            );
          }
          totalBasePrice += this.getTotalBasePrice(item, element.taxRate, true);
        }
      });
      element.comissionAmount = totalComissionAmount.toFixed(2);
      element.totalDeliveryCharge = totalDeliveryCharge.toFixed(2);
      element.totalCanceledAmount = totalCancelPrice.toFixed(2);
      element.totalNonCanceledPrice = totalOutgoingAmount.toFixed(2);
      element.totalBasePrice = totalBasePrice.toFixed(2);
    });
    console.log('Data : ', data.data);
  }
  getTotalSellingPrice(item: any, taxRate: any, food: any) {
    return (
      (food ? item.foodSellingPrice : item.productSellingPrice) *
        (taxRate / 100) +
      (food ? item.foodSellingPrice : item.productSellingPrice) +
      item.shiprocket_delivery_charges
    );
  }
  getTotalBasePrice(item: any, taxRate: any, food: any) {
    return (
      parseInt(food ? item.foodPrice : item.productId.basePrice) *
        (taxRate / 100) +
      parseInt(food ? item.foodPrice : item.productId.basePrice)
    );
  }

