import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { NgxSpinnerService } from 'ngx-spinner';
import { SuperAdminService } from 'src/app/Services/super-admin.service';

@Component({
  selector: 'app-category-list',
  templateUrl: './category-list.component.html',
  styleUrls: ['./category-list.component.scss']
})
export class CategoryListComponent implements OnInit {
  p: number = 1;
  term: any;
  openslider: boolean = false;
  storeType: any;
  element: any = [];
  allSalestrip: any = [];
  updteButton = false;
  regForm!: FormGroup;
  productimgURL: any = null;
  id: any;
  productImgUrl: any = [];
  categoryType = ["Product", "Service", "Food", "Vedio"]
  productimgs: any = [];
  constructor(private formBuilder: FormBuilder, private SpinnerService: NgxSpinnerService,
    private superAdminService: SuperAdminService, private router: Router) { }

  ngOnInit(): void {
    this.regForm = this.formBuilder.group({
      storeType: ['', Validators.required],
      name: ['', Validators.required],
      image: [''],
      productimgs: ['']
    })
    this.SpinnerService.show();
    this.getAllCategory();
  }
  addNew() {
    this.router.navigateByUrl("/superAdmin/category");
  }
  addCategory() {
    console.log(this.regForm.value);

    let formData: any = new FormData();
    formData.append('name', this.regForm.value.name);
    formData.append('storeType', this.regForm.value.storeType);
    formData.append('image', this.regForm.value.productimgs);

    console.log(formData);

    this.superAdminService.addCategory(formData).subscribe((res: any) => {
      this.regForm.reset();
      this.productimgURL = "";
      console.log(res);
      this.getAllCategory();


    })

  }
  showPic(event: any) {
    // this.imageChangedEvent=event;

    let file = (event.target).files[0];
    console.log("file:" + file);
    this.regForm.controls.productimgs.setValue(file, { emitModelToViewChange: false });
   


    const reader = new FileReader();
    reader.onload = () => {
      this.productimgURL = reader.result as string;
      // this.productImgUrl.push(this.productimgURL);
    }
    // this.productimgs.push(file);

    reader.readAsDataURL(file);

  }

  getCategoryById() {
    const id = 1;
    this.superAdminService.getCategoryById(id).subscribe((res: any) => {
      console.log(res);

    })
  }
  getAllCategory() {
    this.superAdminService.getAllCategory().subscribe((res: any) => {
     
      res.forEach((ele: any) => {
        ele.image = "https://s3.wasabisys.com/valart/" + ele.image;
      });
      this.element = res;
      console.log(this.element);
      this.SpinnerService.hide();
    }, (error) => {
      this.SpinnerService.hide();
    })
  }
  cancleUpade() {
    this.updteButton = false;
    this.regForm.reset();
  }
  cancle() {
    this.regForm.reset();
  }
  edit(ele: any) {
    this.router.navigateByUrl('superAdmin/categoryView/' + ele._id);


  }
  updateCategoty() {

  }
  deleteCategory(ele: any) {
    console.log(ele);
    this.SpinnerService.show();
    this.superAdminService.deleteCategoryById(ele._id).subscribe((res: any) => {
      console.log(res);
      this.getAllCategory();
    })

  }
  // clear search
  handleClear(){
    this.term = '';
  }
  clickEvent(event: any) {

    this.openslider = !this.openslider;
  }


}