import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { NgxSpinnerService } from 'ngx-spinner';
import { DataService } from 'src/app/Services/data.service';
import { SuperAdminService } from 'src/app/Services/super-admin.service';

@Component({
  selector: 'app-store-list',
  templateUrl: './store-list.component.html',
  styleUrls: ['./store-list.component.scss'],
})
export class StoreListComponent implements OnInit {
  p: number = 1;
  term: any;
  openslider: boolean = false;
  storeType: any;
  element: any = [];
  headElements = ['Name', 'Store', 'Mobile', 'Address', 'View Store'];
  userData: any;
  StoreDetails: any = [];
  constructor(
    private superAdminService: SuperAdminService,
    private SpinnerService: NgxSpinnerService,
    private router: Router,
    private dataService: DataService
  ) {}

  ngOnInit(): void {
    this.SpinnerService.show();
    this.getAllStore();
  }
  storeDetail(ele: any) {
    this.router.navigateByUrl('/superAdmin/store-detail/' + ele._id);
  }
  foodReviewDetils(ele: any) {
    console.log('food');
    this.router.navigateByUrl('/superAdmin/food-review-details/' + ele);
  }
  productReviewDetils(ele: any) {
    console.log('product');
    this.router.navigateByUrl('/superAdmin/product-review-details/' + ele);
  }
  ServiceReviewDetils(ele: any) {
    console.log('service');

    this.router.navigateByUrl('/superAdmin/service-review-details/' + ele);
  }
  goToStore(ele: any) {
    console.log(ele);
    const data = {
      storeName: ele.name,
      logo: ele.storeLogo,
    };
    localStorage.setItem('storeDetails', JSON.stringify(data));
    localStorage.setItem('storeStatus', ele.verified);
    var currentUser: any = localStorage.getItem('currentUser');
    this.userData = JSON.parse(currentUser);
    const superAdminStoreId = this.userData.user.storeId;
    localStorage.setItem('superAdminStoreId', superAdminStoreId);
    localStorage.setItem('storeFeth', ele._id);
    console.log(superAdminStoreId);

    this.userData.user.storeId = ele._id;

    localStorage.removeItem('currentUser');
    localStorage.setItem('currentUser', currentUser);
    console.log(this.userData);
    this.router.navigateByUrl('admin/dashboard');
  }

  activeStore(ele: any) {
    console.log(ele);
    this.SpinnerService.show();
    this.superAdminService.storeActive(ele).subscribe((res: any) => {
      console.log(res);
      this.element = [];

      this.getAllStore();
    });
  }
  storerReview(ele: any) {
    console.log(ele);

    this.router.navigateByUrl('/superAdmin/store-review-details/' + ele);
  }

  getAllStore() {
    this.superAdminService.allStore().subscribe((res: any) => {
      console.log(
        'res form super admin user',
        res,
        this.superAdminService.allStore
      );
      this.StoreDetails = res.data;
      // this.element = res.data;
      console.log(res.data);

      for (let i of res.data) {
        this.element.push({
          contactPerson: i.contactPerson,
          name: i.name,
          phoneNumber: i.phoneNumber,
          postalAddress: i.postalAddress,
          _id: i._id,
          verified: i.verified,
          store: '',
          storeType: i.storeType,
          storeLogo: i.storeImagesId.storeLogo,
        });
      }
      console.log(this.element);
      this.element.forEach((ele: { verified: boolean; store: string }) => {
        if (ele.verified == false || typeof ele.verified === 'undefined') {
          ele.store = 'Activate Store';
        }
        if (ele.verified == true) {
          ele.store = 'Deactivate store';
        }
      });
      console.log(this.element);
      this.SpinnerService.hide();
    });
  }

  // clear search
  handleClear() {
    this.term = '';
  }
  clickEvent(event: any) {
    this.openslider = !this.openslider;
  }
}
